import classNames from "classnames";
import { useState } from "react";

function FavoriteButton() {

    const [isBookmarked, setIsBookmarked] = useState(false);


    const handleClick = () => {
        setIsBookmarked(!isBookmarked)
    }

    return (
        <button
            className={
                classNames(
                    'bookmark-btn',
                    isBookmarked && 'bookmarked'
                )}
            onClick={handleClick}
        >
            <svg width="16" height="20" viewBox="0 0 16 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path fillRule="evenodd" clipRule="evenodd" d="M0.72644 3.43848C0.72644 2.33391 1.62187 1.43848 2.72644 1.43848H12.7264C13.831 1.43848 14.7264 2.33391 14.7264 3.43848V19.4385L7.72644 15.9385L0.72644 19.4385V3.43848Z" strokeLinecap="round" strokeLinejoin="round" />
            </svg>
        </button>
    )
}

export { FavoriteButton }